import React, {useState, useEffect} from 'react'
import {Link} from 'react-router-dom'

function PreGame(props) {
    const [includeFamily, setIncludeFamily] = useState(false)
    const [questionString, setQuestionString] = useState("1-2-3-4-5")
    useEffect(() => {
        if (includeFamily){
            const toAdd = []
            while (toAdd.length < 5){
                const questionToAdd = Math.floor(Math.random() * 41)
                if (!toAdd.includes(questionToAdd)) toAdd.push(questionToAdd);
            }
            while (toAdd.length < 6){
                const questionToAdd = Math.floor(Math.random() * 8 + 34)
                if (!toAdd.includes(questionToAdd)) toAdd.push(questionToAdd);
            }
            setQuestionString("/game/" + toAdd.join("-"))
        }else{
            const toAdd = []
            while (toAdd.length < 6){
                const questionToAdd = Math.floor(Math.random() * 34)
                if (!toAdd.includes(questionToAdd)) toAdd.push(questionToAdd);
            }
            setQuestionString("/game/" + toAdd.join("-"))
        }
    }, [includeFamily])

    function changeFamily(e){
        setIncludeFamily(prevIF => !prevIF)
    }

    return(
        <div className="flex flex-col items-center space-y-2 m-4">
            <div className="bg-green-500 rounded p-5 space-y-2 md:w-1/2">
                <h1 className="text-3xl">Game Legend</h1>
                <p>
                    This game was created by living in my mom's basement till age 30 unfortunately most of the
                    design elements were lost. When I went to join a growing startup called wework to help them 
                    with their expansion in 2019. Which is why the sight looks the way it does.
                </p>
                <h1 className="text-3xl">What is Known</h1>
                <p> 
                    To play this game choose if you want family questions click the link then send the link to your friend and finally choose your nickname .
                    You and your friend will be shown the questions and you will both answer and access the others person's answer and see how many you each get right.
                    If you are testing this you can open the same link in a different tab.
                </p>
            </div>
            <div className="bg-pink-300 rounded flex flex-col items-center space-x-4 p-3 md:w-1/2 w-full text-xl">
                <div onClick={includeFamily}>
                    <input type="checkbox" name="includeFamily" checked={includeFamily} onChange={changeFamily}/>
                    <label> Include Family Questions</label>
                </div>
                <div className="bg-green-500 rounded text-2xl py-2 px-4">
                    <Link to={questionString}>Start Game</Link>
                </div>
            </div>
        </div>
    )
}

export default PreGame