import React, {useState, useEffect} from 'react'
import GameQuestion from './GameQuestion'
import Socket from './Socket'
import GetName from './GetName'

function DuringGame(props){
    const [quest, setQuest] = useState([])
    const [yanswer, setYAnswer] = useState([])
    const [scoreboard, setScoreBoard] = useState(["user1", "user2", 0, 0])
    const [haveName, setHaveName] = useState(false)
    const [done, setDone] = useState(false)

    useEffect(() => {
        const questionBank = ["How did * get the name *?","Where was * born?","What sports/activities did * participate in while in highschool?","Who does * look up to the most?","What special day(s) does * celebrate that others do not?","What are *'s hobbies?","What accomplishments are * most proud of?","If * were going to be on TV which show would * choose?","What is *'s favorite show?","If * could choose any country to go where would * go?","How many bones has * broken?","What size shoe does * wear?","What is *'s favorite meal?","Does * believe there is extraterrestrial life (aliens)?","What languages does * know and if they know one which do they wish they could learn?","If * could go to another time period and place to visit what would * choose?","If * could go to another time period and place to live what would * choose?","What is *'s favorite exercise?","Which animal does *'s personality most resemble?","If * could have any super power which would * choose?","What does * talk about most often?","How often does * brag about acts of kindness they do?","What topics can * talk about comfortably for hours?","What is *'s favorite ice cream flavor?","What is *'s favorite genre of music?","What is *'s favorite song?","What is *'s favorite climate?","Has * met someone famous and if so who?","What is the scariest thing * has done?","What is the greatest obstacle * has overcome?","How did * decide their profession?","What is *'s most cherished possession?","If * could have only three possessions on an island what would * choose?","What are all the places * has lived?","Which relative does * resemble the most?","What personality and habits does * share with other relatives?","What is *'s favorite family dish?","How did *'s parents choose *'s name?","Does * share a name with another relative?","Who is *'s most famous relative?","What is *'s most important family holiday?","Who is *'s oldest relative?"]
        const questionNumbers = props.match.params.gameId.split("-")
        const questions = questionNumbers.map(num => ({question: questionBank[num], answer: ""}))
        setQuest(questions)
        const nYAnswer = []
        for (let i = 0; i < questions.length; i++){
            nYAnswer.push({answer: "", oright:false})
        }
        setYAnswer(nYAnswer)
    }, [])

    useEffect(() => {
        let total = 0
        for (let i = 0; i < yanswer.length; i++){
            total += yanswer[i].oright ? 1 : 0
        }
        setScoreBoard(prevVal => {
            let nArr = [...prevVal]
            nArr[3] = total
            return nArr
        })
    }, [yanswer]);

    let userAnswer = (string, index) => {
        setYAnswer(prevAnswer => {
            const newArr = [...prevAnswer]
            newArr[index]['answer'] = string
            return newArr
        })
    }

    let userOAnswer = (value, index) => {
        setYAnswer(prevAnswer => {
            const newArr = [...prevAnswer]
            newArr[index]['oright'] = value
            return newArr
        })
    }

    let doneWithGame = () => {
        setDone(true)
    }

    return(
        <div className="flex flex-col items-center space-y-4 m-4 w-full">
            {!haveName && <GetName setHaveName={setHaveName} setScoreBoard={setScoreBoard}/>}
            <div className="flex flex-row justify-evenly flex-wrap space-x-1 space-y-2">
                {(haveName && !done ) && quest.map((item, index) => (
                    <GameQuestion question={item.question} answer={item.answer} other={scoreboard[1]} index={index} key={index} changeAnswer={userAnswer} changeOright={userOAnswer} yanswer={yanswer}/>
                    ))}
            </div>
            {(haveName && !done ) && <Socket yanswer={yanswer} name={scoreboard[0]} room={props.match.params.gameId} setQuest={setQuest} setScore={setScoreBoard}/>}
            {(haveName && !done) && <button className="rounded text-3xl md:w-1/3 w-1/2 h-20 bg-pink-500"onClick={doneWithGame}>Done</button>}
            {done && <div className="flex flex-col items-center bg-pink-500 md:w-1/3 w-1/2 h-32 rounded text-3xl justify-evenly"><h1>Your Score: {scoreboard[2]}</h1><h1>{scoreboard[1]}: {scoreboard[3]}</h1></div>}
        </div>
    )
}

export default DuringGame