import React, {useState} from 'react'

function GameQuestion(props){
    const [yanswer, setYAnswer] = useState("")
    const [oright, setOright] = useState(props.yanswer[props.index].oright)

    let changeA = e => {
        setYAnswer(e.target.value)
        props.changeAnswer(e.target.value, props.index)
        e.persist()
    }

    let changeO = e => {
        if(e.target.value !== oright.toString()){
            setOright(!oright)
            props.changeOright(e.target.value, props.index)
        }
    }

    return(
        <div className="bg-blue-500 rounded-lg md:w-2/5 xl:w-1/4 p-4 border-white flex flex-col">
            <div>
                <h3 className="m-1 text-xl">{props.question.split("*").join(props.other)}</h3>
            </div>
            <div className="">
                <textarea placeholder="Type your answer here" value={yanswer} className="p-2 m-1 w-full rounded-md h-40" onChange={changeA}>
                </textarea>
                <textarea id="other" placeholder="Their Answer Here" value={props.answer} className="p-2 m-1 w-full rounded-md h-40" disabled>
                </textarea>
            </div>
            <div className="flex flex-row space-x-2">
                <button className={"w-1/2 rounded " + (oright ? "bg-green-500 " : "bg-green-200 ")} value="true" onClick={changeO}>âœ“</button>
                <button className={"w-1/2 rounded " + (oright ? "bg-red-200 " : "bg-red-500 ")} value="false" onClick={changeO}>X</button>
            </div>
        </div>
    )
}

export default GameQuestion