import React, {useState} from 'react'

export default function GetName(props){
    const [name, setName] = useState("Dafaq")

    let changeName = (e) => {
        setName(e.target.value)
    }

    let submit = () =>{
        props.setScoreBoard(prevScore => {
            const nScore = [...prevScore]
            nScore[0] = name
            return nScore
        })
        props.setHaveName(true)
    }

    return(
        <div className="flex flex-col rounded bg-pink-500 w-1/2 xl:w-1/3 items-center p-6 space-y-2">
            <div>
                <p className="text-2xl">What is your name?</p>
            </div>
            <div className="space-x-2">
                <input className="rounded px-1" type="text" name="UserName" placeholder="Dafaq" value={name} onChange={changeName} />
                <button className="rounded bg-blue-300 w-24" onClick={submit}>Submit</button>
            </div>
        </div>
    )
}