import React from 'react'
import socketIOClient from 'socket.io-client'

class Socket extends React.Component{
    state = {
        socket: null,
        name: this.props.name,
        room: this.props.room,
    }

    componentDidMount(){
        const socketio = socketIOClient()
        const username = this.state.name
        const room = this.state.room
        socketio.emit('joinChat', {username, room})
        socketio.on('users', data => {
            console.log(data)
            for (let i = 0; i < data.users.length; i++){
                if(data.users[i].username !== username){
                    this.props.setScore(prevScore => {
                        const nArr = [...prevScore]
                        nArr[1] = data.users[i].username
                        return nArr
                    })
                }
            }
        })
        setInterval(() => {
            const answer = this.props.yanswer
            socketio.emit("clientMessage", { answer })
        }, 5000)
        socketio.on("hostMessage", data => {
            this.props.setQuest(prevQuest => {
                const nArr = [...prevQuest]
                for(let i = 0; i < data.text.answer.length; i++){
                    nArr[i]['answer'] = data.text.answer[i]['answer']
                }
                return nArr
            })
            let total = 0
            for (let i = 0; i < data.text.answer.length; i++){
                total += data.text.answer[i].oright ? 1 : 0
            }
            this.props.setScore(prevScore => {
                const nArr = [...prevScore]
                nArr[2] = total
                return nArr
            })
        })
        this.setState({ socket: socketio })
    }

    componentWillUnmount(){
        if (this.state.socket != null){
            this.state.socket.disconnect()
        }
    }

    render(){
        return(
            <br/>
        )
    }
}

export default Socket